<template>
  <div>
    <v-header :isOther="true"/>
    <div class="buy-sucess">
      <div class="web">
        <p class="add-tip">
          <Icon type="md-checkmark-circle" color="#71b247" size="36" class="mr8" />
          <span>商品已成功加入购物车！</span>
        </p>
        <Row>
          <Col span="2">
            <img :src="getShopingCart[cartNum-1].goodsImg" alt class="pro-img" />
          </Col>
          <Col span="15" offset="1">
            <p class="pro-name">{{getShopingCart[cartNum-1].goodsName}}</p>
            <p>数量:{{getShopingCart[cartNum-1].num}}</p>
          </Col>
          <Col span="6">
            <Button type="primary" ghost class="mr8" @click="$router.back()">返回商品详情</Button>
            <Button type="primary" @click="$router.push('/cart')">去购物车结算</Button>
          </Col>
        </Row>
      </div>
    </div>
    <v-footer />
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["getShopingCart"]),
    cartNum() {
      return this.getShopingCart.length;
    }
  },
  methods: {
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/theme.less";
.buy-sucess {
  background-color: @grayBg;
  padding: 10px 0;
  button {
    font-size: 18px;
  }
}
.add-tip {
  font-size: 18px;
  color: #71b247;
  margin-bottom: 10px;
}
</style>



